.about-me {
    display: flex;
    justify-content: space-between;
    align-items: center;
    //width: 100%;
    height: 100vh;
    background: #141414;
    background: linear-gradient(143deg, rgba(49, 49, 49, 0.5) -19%, #0e0d0d 60%);
    padding: 0 5rem;
    background-size: 200% 200%;
    animation: gradientBlock 10s ease infinite;

    @keyframes gradientBlock {
      0% {
        background-position: 0 50%;
      } 
      50% {
        background-position: 100% 50%;
      }
      100% {
        background-position: 0 50%;
      }
    }

  @media screen and (max-width: 1000px) {
      display: block;
      padding: 0 2rem;
      overflow-y: scroll;
  }


    .about-me-text {
        position: relative;
        z-index: 2;
        width: 600px;

        @media screen and (max-width: 1000px) {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
        }


        @media screen and (max-width: 550px) {
          display: block;
          margin-top: 0%;
          padding-top: 3rem;
        }

        .span-text {
            padding: 0;

            @media screen and (max-width: 1000px) {
              font-size: 3rem;
            }

            @media screen and (max-width: 550px) {
              font-size: 2rem;
              text-align: center;
              margin: 0;
            }
        }
        
        .text-me {
            text-align: justify;
            margin-top: 5rem;
            line-height: 1.5;
            margin-bottom: 2rem;
            font-size: 1.8rem;
            
        }
        
        .link-add-team {
            text-decoration: none;
            color: #08FDD8;
            
            &:hover {
                text-decoration: underline;
            }
        }

        .social {
          margin-top: 3rem;
          display: flex;
          justify-content: center;
          align-items: center;

          @media screen and (max-width: 550px) {
            padding-bottom: 3rem;
          }
  
          a {
              margin-right: 2rem;
  
              img {
                  width: 1.8rem; 
              }
  
              &:last-child {
                  margin-right: 0rem;
                  img {
                      width: 12px;
                  }
              }
          }
      }
    }

    .hole {
        position: relative;
        right: 20%;
        z-index: 1;
    
        transform: scale(2);
        transition: transform .2s linear;
        
        & .planet {
        width: 400px;
        height: 400px;
        position: relative;
        z-index: 2;
        margin: auto;
        border-radius: 50%;
        transform: rotateZ(0deg);

        @media screen and (max-width: 1000px) {
          display: none;
        }

    
